html, body, #root {
  height: 100%;
  width: 100%;
  font-family: 'Roboto', sans-serif;
}

.TicTacToe {
  margin: 0 auto;
}

h1 {
  text-align: center;
  margin-top: 10px;
}

.Grid {
  margin: 0 auto;
  border-collapse: collapse;
  border-spacing: 0;
}

.Grid-true {
  background: #eee;
}

td {
  border: 1px solid black;
  width: 130px;
  height: 130px;
  text-align: center;
  font-size: 30px;
}

.buttons {
  height: 44px;
  width: 224px;
  margin: 20px auto;
}
button {
  cursor: pointer;
  height: 40px;
  width: 100px;
  font-size: 14px;
  border: none;
  margin: 0 5px;
}

button:hover {
  border: 2px solid white;
}

button:active, button:focus {
  outline: none;
}

.black-bg {
  background: #222222;
  color: white;
}

.red-bg {
  background: #f9aba7;
  color: black;
}

.Tally td {
  height: 30px;
  font-size: 15px;
}

.grey-bg {
  background: #eee;
}

@media (max-width: 500px) {
  td {
    width: 100px;
    height: 100px;
  }
}



